.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

button {
  cursor: pointer;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.quiz-list {
  display: table;
  width: 90%;
  margin: 0 auto;
}

.quiz-list h2 {
  width: 200px;
  margin: 0 auto;
}

.quiz-list-item {
  display: table-row;
}

.quiz-list-item > * {
  display: table-cell;
  padding: 5px;
}

.user-result {
  width: 30%;
  margin: 0 auto;
  text-align: center;
}

.quiz-questions-section {
  width: 30%;
  margin: 0 auto;
  text-align: center;
}

.statistics {
  width: 30%;
  margin: 0 auto;
  text-align: center;
}
